import { Component } from "react";
import './profile.css';
import avatar from '../../assets/images/avatar_1.png'

export default class Profile extends Component {
    level = 29;
    achivment = "Не только Асигнер но и беда для разработчиков - Ревьюер";
    name = "Долгов Константин";
    description = "C# разработчик с наклонностями художника\n" +
        "и с нотками перфекционизма";
    profile_avatar = "";

    render() {
        return(
            <div className="profiles_block">
                <div className="profile_area">
                    <img src={avatar} alt="profile avatar" width="240px" height="240px"/>
                </div>
                <p className="title_area">{this.name}</p>
                <p className="description_area">{this.description}</p>
                <div className="level_area">Уровень:
                    <span>{this.level}</span>
                </div>
                <div className="achivment_area">Достижение:
                    <p>{this.achivment}</p>
                </div>
                <div className="edit_button_area">
                    <button className="edit_button">Редактировать профиль</button>
                </div>
            </div>
        );
    }
}